/** HEADER **/
.Header {
  text-align: center;
  outline: none;
  user-select: none;
  padding: 0.7em;
  @mixin for-phone-only {
    margin: 0.3em auto;
    width: 80%;
  }

  @mixin for-phone-only {

  }
  @mixin for-tablet-portrait-up {

  }
  @mixin for-tablet-landscape-up {

  }
  @mixin for-desktop-up {

  }
  @mixin for-big-desktop-up {

  }
}
.HeaderButton {
  display: inline-block;
  width: 10em;
  @mixin for-phone-only {
    margin-top: 0.3em;
    width: 7em;
  }
}
.Header a {
  letter-spacing: 1px;
  text-decoration: none;
  font-size: 1.3em;
  @mixin for-phone-only {
    font-size: 1.1em;
  }
}
.HeaderButton a {
  padding-bottom: 5px;
  background: linear-gradient(#565656, #565656) no-repeat center bottom;
  background-size: 0 2px;
  transition: all 0.15s;
}
.HeaderButton a:hover {
  background-size: 100% 2px;
  @mixin for-phone-only {
    font-weight: normal;
  }
}
.HeaderBackButton a {
  padding-bottom: 5px;
  display: inline-block;
  background: linear-gradient(#565656, #565656) no-repeat center bottom;
  background-size: 0 2px;
  transition: all 0.1s;
}
.HeaderBackButton a:hover {
  background-size: 100% 2px;
}
@media screen and (min-width: 640px) and (max-width: 959px) {
  .Header {
    margin: auto;
    width: 90%;
  }
  .HeaderButton {
    width: 9em;
  }
  .Header a:hover {
    font-weight: normal;
  }
}

