/** TECHNOLOGIES **/
.Technologies {
  width: 40em;
  margin: 6em auto 2em;
  text-align: center;
}
.Technologies h1 {
  font-size: 2.5em;
}
.TechnologiesIconPlaceHolder {
  margin: 3em auto;
  text-align: center;
  width: 40em;
}
.TechnologiesIcon {
  align-content: center;
  transform: scale(1.3);
  transition-delay: 0s;
  transition-duration: 0.15s;
  transition-property: all;
  transition-timing-function: ease;
  display: inline-block;
  margin: 0 2em 0 2em;
  background: rgba(0, 0, 0, 0.005);
}
.TechnologiesIcon:hover .Popup {
  display: block;
}
.TechnologiesIcon:hover {
  transform: scale(1.45);
}
.Popup {
  font-size: 0.9em;
  position: absolute;
  width: 7em;
  margin: 0 auto;
  transform: translateX(-2.05em);
  display: none;
  border-radius: 2em;
  padding: 0.3em;
  box-shadow: rgb(211, 211, 211) 1px 1px 1px;
  background: rgb(255, 255, 255);
  border: 0.1em solid rgba(75, 71, 71, 0.42);
}

@media screen and (max-width: 639px) {
  .Technologies {
    margin: auto;
    width: 90%;
  }

  .TechnologiesIconPlaceHolder {
    margin: 1em auto;
    width: 90%;
  }

  .TechnologiesIcon {
    margin: 0 1.5em 0 1.5em;
  }

  .TechnologiesIcon:hover {
    transform: none;
  }

  .TechnologiesIcon:focus {
    transform: none;
  }

  .TechnologiesIcon:active {
    transform: none;
  }

  .TechnologiesIcon:hover .Popup {
    display: none;
  }
}
@media screen and (min-width: 640px) and (max-width: 959px) {
  .Technologies {
    font-size: 1.5em;
    margin: auto;
    width: 90%;
  }

